<template>
<v-container>
  <v-form ref="form" v-model="valid" lazy-validation>
    <v-text-field v-model="apiData.name" :counter="20" :rules="rules.name" label="" required></v-text-field>
    <v-text-field v-model="apiData.title" :counter="20" :rules="rules.title" label="" required></v-text-field>
    <v-text-field v-model="apiData.uri" :counter="20" :rules="rules.uri" label="URI" required></v-text-field>
    <v-select v-model="apiData.method" :items="methods" :rules="rules.method" label="Method" required></v-select>
    <v-select v-model="apiData.category_id" :items="categories" item-text="title" item-value="_id" :loading="categoryLoading" label="" required></v-select>
    <v-checkbox v-model="apiData.authentication" label="Authentication "></v-checkbox>
    <v-checkbox v-model="apiData.authorization" label="Authorization "></v-checkbox>
    <v-btn class="mr-5" @click="$router.go(-1)"></v-btn>
    <v-btn color="primary" @click="validate">  </v-btn>
  </v-form>
  <v-dialog v-model="loading" persistent width="300">
    <v-card color="primary" dark>
      <v-card-text>
        
        <v-progress-linear indeterminate color="white" class="mb-0"></v-progress-linear>
      </v-card-text>
    </v-card>
  </v-dialog>
</v-container>
</template>

<script>
import { getApi, getApiGroup, addApi, editApi } from '@/plugins/api'
export default {
  data: () => ({
    apiData: {
      name: '',
      title: '',
      method: 'get',
      authentication: true,
      authorization: true,
      category_id: null
    },
    rules: {
      name: [
        v => !!v || '',
        v => (v && v.length <= 20) || '20'
      ],
      title: [
        v => !!v || '',
        v => (v && v.length <= 20) || '20'
      ],
      uri: [
        v => !!v || 'URI',
        v => (v && v.length <= 20) || '20'
      ],
      method: [v => !!v || '']
    },
    valid: true,
    methods: ['get', 'post', 'put', 'delete', 'patch'],
    categories: [{ _id: null, title: '' }],
    categoryLoading: false,
    loading: false
  }),
  mounted () {
    this.categoryLoading = true
    getApiGroup().then(res => {
      this.categoryLoading = false
      this.categories = this.categories.concat(res.data.categories)
    })
    if (this.$route.name === 'API_EDIT') {
      this.loading = true
      getApi({ _id: this.$route.params.id }).then(res => {
        this.loading = false
        this.apiData = res.data.apis[0]
        console.log(res.data)
      })
    }
  },
  methods: {
    validate () {
      this.$refs.form.resetValidation()
      if (this.$refs.form.validate()) {
        console.log(this.apiData)
        if (this.apiData._id) {
          editApi(this.apiData).then(res => {
            console.log(res.data)
            this.$router.go(-1)
          })
        } else {
          addApi(this.apiData).then(res => {
            console.log(res.data)
            this.$router.go(-1)
          })
        }
      }
    }
  }
}
</script>
